/*Footer*/
footer {
    background-color: var(--bg);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 90vh;
  }
  .footer-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .footer-content h2 {
    font-family: 'Quicksand', sans-serif;
  }
  .footer-content h1 {
    color: var(--sg);
  }
  .footer-socials {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: fit-content;
  }
  .footer-socials li {
    float: left;
    padding: 10px;
  }
  .footer-socials a:after {
    content: '';
    display: block;
    margin: auto;
    height: 2px;
    width: 0px;
    background: transparent;
    transition: width 200ms ease-out, background-color 400ms ease;
  }
  .footer-socials a:hover:after {
    width: 80%;
    background: var(--ltext);
  }
  .footer-wrapper hr {
    width: 80vw;
    color: var(--ltext);
  }
  .end-note {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 80%;
  }