.section-two {
  background-color: var(--bg2);
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 64px 64px 0 0;
  padding: 100px 12vw 0 12vw;
  gap: 20px;
}
.section-two-card-wrapper {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 0 3vw 0 3vw;
}
.section-two-card-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 50px;
}
.section-two-card {
  height: 150px;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.section-two-card-title {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}