/*Root*/
@import url('https://fonts.googleapis.com/css2?family=Quicksand&family=Urbanist:wght@800&display=swap');
:root {
  --bg: #ffffff;
  --bg2: #f8f8f8;
  --text: #292929;
  --ltext: #494949;
  --sg: #78866b;
  --lsg: #8f9779;
  --border: #bbbbbb;
  --select: #b8c0a3;
  --p: 18px;
  --a: 18px;
  --h1: 72px;
  --h2: 36px;
  --h3: 18px;
  --h4: 36px;
  --b: 14px;
}
::selection {
  background: var(--lsg);
  color: var(--bg);
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: var(--bg2); 
}
::-webkit-scrollbar-thumb {
  background: var(--border); 
}
::-webkit-scrollbar-thumb:hover {
  background: var(--sg);
}
h1 {
  font-family: 'Urbanist', sans-serif;
  font-size: var(--h1);
  color: var(--text);
  padding: 0;
  margin: 0;
}
h2 {
  font-family: 'Urbanist', sans-serif;
  font-size: var(--h2);
  color: var(--text);
  padding: 0;
  margin: 0;
}
h3 {
  font-family: 'Urbanist', sans-serif;
  font-size: var(--h3);
  color: var(--text);
  padding: 0;
  margin: 0;
}
h4 {
  font-family: 'Quicksand', sans-serif;
  font-size: var(--h4);
  color: var(--ltext);
  padding: 0;
  margin: 0;
}
p {
  font-family: 'Quicksand', sans-serif;
  font-size: var(--p);
  color: var(--ltext);
  padding: 0;
  margin: 0;
}
ul, a, li {
  text-decoration: none;
  list-style: none;
  font-family: 'Quicksand', sans-serif;
  font-size: var(--a);
  color: var(--ltext);
  padding: none;
  margin: none;
  list-style-type: none;
  padding-inline-start: 0;
}
button {
  font-family: 'Urbanist', sans-serif;
  font-size: var(--b);
  color: var(--ltext);
  padding: 0;
  margin: 0;
  text-align: center;
  align-items: center;
}
body {
  margin: 0;
  padding: 0;
}
html {
  scroll-behavior: smooth;
}
