.section-one {
  background-color: var(--bg);
  height: 90vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 40px;
}
.section-one img {
  max-width: 350px;
  height: auto;
  border-radius: 50%;
  border: solid 16px var(--bg);
  box-shadow: 12px 12px 40px #5f5f5f75;
  z-index: 1;
}
.section-one-wrapper {
  text-align: left;
  z-index: 1;
  overflow: hidden;
}
.section-one-wrapper h1 {
  animation: 300ms 50ms slideIn;
  transform: translateY(-100px);
  animation-fill-mode: forwards;
}
@keyframes slideIn{
  0%{
    opacity: 0%;
  }
  100%{
    transform: translateY(0px);
    opacity: 100%;
  }
}
.section-one-circle {
  position: absolute;
  top: -25vw;
  right: 0vw;
  background-color: #6d6d6d20;
  border-radius: 50%;
  height: 800px;
  width: 800px;
  filter: blur(80px);
  z-index: 0;
}
.section-one-button-wrapper {
  display: flex;
  justify-content: left;
  align-items: center;
  padding: 20px 0 0 0;
  gap: 20px;
}
.section-one-button-wrapper a {
  border-radius: 32px;
}
.section-one-button {
  width: 120px;
  height: 40px;
  border-radius: 32px;
  box-shadow: 2px 2px 10px #6d6d6d20;
  background-color: var(--bg);
  transition: 100ms ease-in;
  cursor: pointer;
  border: solid 2px var(--border);
}
.section-one-button:hover {
  background-color: var(--sg);
  transition: 50ms;
  color: var(--bg);
  border: solid 2px var(--sg);
}